.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

a {
    color: #00c1ff;
    text-decoration: none;
}

.courselist {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    list-style: none;
    margin: 60px 0 20px;
    padding: 0;
    justify-content: center;
}

.courses-form>div {
    padding: 0 11px;
}

.courses-sci {
    float: left;
    box-sizing: border-box;
    overflow: hidden;
    display: block;
    margin: 0 15px 55px;
    width: 170px;
    min-height: 260px;
    border: solid 1px #b5b5b5;
    border-radius: 10px;
    background: #fff;
}

.sci-figure {
    position: relative;
    overflow: hidden;
    height: 134px;
    background: #ddd;
}

.sci-figure img {
    max-width: 100%
}

.sci-info {
    padding: 10px 16px;
}

.sci-info>.sci-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.sci-info>.sci-grade {
    margin-top: -.2em;
}

.sci-info>.sci-genre {
    margin-top: -.4em;
    font-size: 11px;
}

.sci-info>p,
.cart-header-msg.sci-info>li {
    margin: .4em 0;
    margin-top: 0.4em;
    padding-left: .5em;
    font-size: 14px;
    font-weight: normal;
}

.sci-info>.sci-meta {
    margin-bottom: 0;
    font-size: 12px;
    font-weight: 600;
    color: #00c1ff;
}

.scipricebonus {
    display: none
}

.bonus .sciprice {
    display: none
}

.bonus .scipricebonus {
    display: block
}

.hidden {
    display: none
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.filter-menu {
    display: flex;
    justify-content: space-between;
    max-width: 1440px;
    margin: 0 auto;
}

.filter-menu>div {

    width: 25%;
    margin:0 11px;
}

.filter-menu select {
    width: 20%;
    display: inline-block;
    font-size: 14px;
    margin: 0 11px;
    vertical-align: middle;
    border-radius: 6px;
    padding: .3em .5em;
    height: 2.3em;
    background-color: #fff;
    border: 1px solid #706f6f;
}

.priceSwitch {
    max-width: 100px;
}

.filter-menu input[type="text"] {
    box-sizing: border-box;
    display: inline-block;
    padding: .4em .5em;
    font-size: 14px;
    vertical-align: middle;
    border: 1px solid #706f6f;
    border-radius: 6px;
    height: 2.3em;
    float: left;
}

.filter-menu input[type="text"]:focus,
.filter-menu select:focus {
    outline: 0;
    border-color: #129fea;
}

.price-block {
    padding: .4em .1em;

    border-radius: 6px;
    text-align: center;
    background: #00c1ff;
    color: #fff;
}

.courses-form-search-btn {

    display: inline-block;
    margin-left: 6px;
    width: 32px;
    height: 32px;
    line-height: normal;
    background: #fff url("icon-search-25.png") center no-repeat;
    border: 0;
    border-radius: 4px;
    cursor: pointer;

}

header h1 {
    font-family: "Segoe UI", "Helvetica Neue", Arial, sans-serif;

}

.center {
    text-align: center;
}

header {

    background: #fff;
    box-shadow: 0 2px rgba(0, 0, 0, 0.2);

}

.header {

    margin: 0 auto;
    padding: 0 20px;

    min-height: 73px;

}

.header-logo {

    display: block;
    margin: 10px auto;
    width: 220px;
    height: 50px;
    background: url("logo.svg") center no-repeat;
    background-size: auto;
    background-size: 100%;
   

}

.header-logo>h1 {
    font-size: 36px;
    margin: 0;
    text-indent: -9999px;

}

@media screen and (min-width: 58em) {
  .header-nav > .header-btn {
      float: left;
      width: 145px;
  }


  .header-nav {
      float: right;
      padding: 17px 0 0 0px;
  }


      .header-nav > .header-cart {
          float: left;
          margin-left: 5px;
      }
      .header-logo {

        float: left;
    
    }
    .header-nav>.header-btn {

      float: left;
      width: 145px;
  
  }
  .header-nav>.header-btn {margin-bottom:0}
}

.header-nav .header-btn-primary {

    background: linear-gradient(#00c0ff, #00c0ff) !important;

}

.header-nav {

  padding: 17px 0 15px;

}

.header-nav>.header-btn {
    color: #fff;
    text-align: center;
    box-sizing: border-box;
    display: block;
    padding: 9px 10px !important;
    height: 43px;
    font-size: 17.5px !important;
    border-radius: 10px;
    font-weight: 300;
    letter-spacing: .03em;
    margin: 0 4px 10px;

}